import noData from '@/components/noData'
import back from '@/components/back'

export default {
    components:{
        noData,
        back
    },
    data(){
        return{
			user: this.$store.state.userInfo,
			dconfirmShow:false,
			list:[],
			title:'编辑',
			tf:false,
			FaceConsultationAddressId:null,
			IsAllowRecommend: null,
			recommendFee: null,
			value2: false
        }
    },
    created(){
	   this.pull()
	   
    },
    mounted(){  
	this.getServiceCharge()
    },
    beforeDestroy(){
        window.onscroll = null
    },
    methods: {
		pull(){
			this.$get('AppointmentAPI/FaceConsultation/QueryFaceConsultationAddress',{
				DrId:this.user.Data.User.DoctorId,
			}).then(o => {
				this.list = o
				this.IsAllowRecommend = o[0].IsRecommend + ''
			})
		},
		getServiceCharge() {
			this.$get('/PlatFormAPI/ServicePackItem/QueryServiceItemDivideRation', {
				DrId: this.user.Data.User.DoctorId,
				SericeItemCode: 1120
			}).then(d => {
				this.recommendFee  = d.ItemDivideRation*100
			})
		},
		onClick(newVal) {
			this.$post('AppointmentAPI/FaceConsultation/SaveDrServiceItemIsRecommend',{
				DrId: this.user.Data.User.DoctorId,
				IsRecommend: newVal
			}).then( d => {

			})
		},
		isEdit(){
			if(this.tf){
				this.tf = false
				this.title = "编辑"
			}else{
				this.tf = true
				this.title = "保存"
			}
		},
		del(i){
			this.dconfirmShow = true
			this.FaceConsultationAddressId = i.FaceConsultationAddressId
		},
		onCancel(){
			this.dconfirmShow = false
		},
		goAddAddress(){
			this.$store.dispatch('pushhospitalInfo',{})
			this.$store.dispatch('pusharea',null)
			this.$router.push('/faceConsultation/AddAddress')
		},
		goId(i){
			this.$store.dispatch('pushhospitalInfo',{})
			this.$store.dispatch('pusharea',null)
			this.$router.push('/faceConsultation/AddAddress?FaceConsultationAddressId='+i.FaceConsultationAddressId)
		},
		pullFaceId(i){
			this.$store.dispatch('pushsaveFaceSet',null)
			this.$store.dispatch('pushFaceId',i.FaceConsultationAddressId)
			this.$router.push('/faceConsultation/SetAppointment')
		},
		onConfirm(){
			this.$post('AppointmentAPI/FaceConsultation/DelFaceConsultationAddress',{
				FaceConsultationAddressId:this.FaceConsultationAddressId
			}).then(o => {
				this.dconfirmShow = false
				this.pull()
			})
		},
		
    }
}



